<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <edit v-if="state" @search="search" :id="id" :name="name" @dialogState="dialogState"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from "views/common/options";
import Tables from "views/common/tables";
import queryString from "querystring";
import Edit from "./control/edit";
import page from "data/page";
import { ScondDay } from "utils/formData";
import { getInvitList, getDealList, getSchool, getClass, getUsersAccount } from "data/listBody";

const dataOption = {
  leftMenu: [
    { 
        name: "邀请人姓名：", 
        placeholder: "请输入邀请人姓名", 
        module: "userId",
        type:'select'
    },
    { 
        name:'校区：', 
        placeholder:'请选择校区名称', 
        module:'schoolId', 
        type:'select'
    },
    { 
        name:'班级：', 
        placeholder:'请选择班级名称', 
        module:'classId', 
        type:'select'
    },
    {
      name: "邀请人手机号：",
      placeholder: "请输入邀请人手机号",
      module: "mobile"
    },
    {
      name: "邀请状态：",
      placeholder: "请选择邀请状态",
      module: "checkState",
      type: "select",
    },
    {
      name: "成交类型：",
      placeholder: "请选择成交类型",
      module: "dealState",
      type: "select",
    }
  ],
  rightMenu: [{ id: "r1", name: "查询" }]
};

const dataTable = {
  table: [
    { label: "校区", prop: "schoolName" },
    { label: "带班老师", prop: "teacherName" },
    { label: "邀请人", prop: "userName" },
    { label: "被邀请人", prop: "name" },
    { label: "邀请状态", prop: "checkStateName" },
    { label: "邀请人手机号", prop: "mobile" },
    { label: "成交类型", prop: "dealStateName" },
    { label: "提交日期", prop: "time" },
    { label: "备注", prop: "remark" }
  ],
  edit: [{ name: "编辑", id: "t1" }]
};

const tips = ["确认删除该条信息?"];

export default {
  name: "invite",
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: JSON.parse(JSON.stringify(dataTable)),
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      state: false, //编辑弹框
      name:"",
      mobile:"",
      checkState:"",
      dealState:"",
      dealList: null,
      invitList: null,
      currentPage: 1,
      id: ""
    };
  },
  components: {
    Options,
    Tables,
    Edit
  },
  mounted() {
    this.init();
    this.dropInit();
    this.search();
  },
  activated(){
    this.search();
  },
  methods: {
    init() {
        const { type, id } = this.$route.query;
        if (type === "detail") return (this.dataTable.edit = []);
        this.dataTable = dataTable;
    },
    dropInit() {
        getInvitList(res => {
            let { leftMenu } = this.dataOption;
            this.invitList = res;
            leftMenu = leftMenu.map(item => {
            if (item.module === "checkState") item.options = res;
            return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getDealList(res => {
            let { leftMenu } = this.dataOption;
            this.dealList = res;        
            leftMenu = leftMenu.map(item => {
            if (item.module === "dealState") item.options = res;
            return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getSchool(res => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if (item.module === "schoolId") item.options = res;
                return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
        getUsersAccount(res => {
            let { leftMenu } = this.dataOption;
            leftMenu = leftMenu.map(item => {
                if (item.module === "userId") item.options = res;
                return item;
            });
            this.dataOption.leftMenu = leftMenu;
        });
    },
    optchange(params) {
        console.log(params.id + "--id被单击了");
        if (params.id === "r1") return this.search();
    },
    tabchange(params) {
        this.id = params.data.id;
        this.name = params.data.name;
        // 编辑
        if (params.id === "t1") return (this.state = true);
    },
    sure(){
        this.api.delete(`/invite/${this.id}`).then((res)=>{
            this.loading = false;
            this.search();
            this.$message(res.data);     
        }).catch((err) => {
            console.log(err)
            this.$message(err.data.message)
        })
        this.dialogVisible = false;
    },
    search(){  //搜索
        const optParams = this.$refs.goodsOpt.getData();
        const pageParams = { size: 20, page: this.currentPage -1 };
        const params = Object.assign({}, optParams, pageParams);
        const param = queryString.stringify(params || {});
        this.loading = true;
        this.api.get(`/invite?${param}`).then((res)=>{
            this.loading = false;
            this.dataInit(res.data);
            this.setPage(res.data);
            this.tableList = res.data.list;
        }).catch((err) => {
            console.log(err)
            this.$message(err.data.message)
        })
    },
    dataInit(data){//数据初始化
        const tableList = data.list.map(item => {
            let inivtArr = this.invitList.filter(obj => {
                return obj.id === item.checkState
            })
            let inivtArr2 = this.dealList.filter(obj => {
                return obj.id === item.dealState
            })
            if(item.createTime) item.time = ScondDay(item.createTime);            
            if( inivtArr.length > 0 ){
                item.checkStateName = inivtArr[0].dValue;
            }
            if( inivtArr2.length > 0 ){
                item.dealStateName = inivtArr2[0].dValue;
            }
        })
        this.tableList = tableList;
    },    
    dialogState() { //弹框状态改变
      this.state = false;
    }
  }
};
</script>
 
<style lang="less" scoped>
</style>
 