<template>
    <el-dialog title="编辑" width="500px" :visible.sync="dialogTableVisible" v-loading="loading">
        <div class="dialog-content">
            <div class="list">
                <span class="require"><i>邀请人姓名：</i></span>
                <span class="text">{{name}}</span>
            </div>
            <div class="list">
                <span class="require"><i>邀请状态：</i></span>
                <el-select class="com-input" v-model="checkState" placeholder="请选择邀请状态">
                    <el-option v-for="item in invitList" :key="item.id" :label="item.dValue" :value="item.id" />
                </el-select>
            </div>
            <div class="list">
                <span class="require"><i>成交类型：</i></span>
                <el-select class="com-input" v-model="dealState" placeholder="请选择成交类型">
                    <el-option v-for="item in dealList" :key="item.id" :label="item.dValue" :value="item.id" />
                </el-select>
            </div>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="cancle">取 消</el-button>
            <el-button type="primary" @click="submit">确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
import { getInvitList, getDealList } from "data/listBody";
import queryString from "querystring";

export default {
  name: "systemUseradd",
  props: ["id","name"],
  data() {
    return {
      dialogTableVisible: true,
      invitList: null,
      dealList: null,
      checkState: "",
      dealState: "",
      loading: false,
    };
  },
  mounted() {
    this.dropInit();
  },
  methods: {
    dropInit() {
        getInvitList(res => {
            this.invitList = res;
        },'filter');
        getDealList(res => {
            this.dealList = res;
        },'filter');
    },
    cancle() {
        this.$emit("dialogState");
    },
    submit() {
        const param = {
            id: this.id,
            name: this.name,
            checkState: this.checkState,
            dealState: this.dealState,
        }
        this.loading = true;
        this.api.put(`/invite`, param, 'form').then((res)=>{
            this.loading = false;
            this.$emit("search");
            this.$emit("dialogState");
        }).catch((err) => {
            console.log(err)
            this.loading = false;
            this.$message(err.data.message)
        })
    }
  }
};
</script>
<style lang="less" scoped>
.dialog-content {
  .list {
    display: flex;
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
    span {
      display: inline-block;
      min-width: 120px;
      text-align: right;
      &.text {
        text-align: left;
      }
      i {
        display: inline-block;
        font-style: normal;
      }
    }
  }
}
</style>